.embed-responsive {
  position: relative;
  width: 100%;
}

.embed-responsive:before {
  display: block;
  content: "";
}

.embed-responsive-item,
.embed-responsive iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.embed-responsive-16by9::before {
  padding-top: 56.25%;
}

.embed-responsive-4by3::before {
  padding-top: 75%;
